body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.find-replace-section {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.find-replace-section input {
  flex: 1;
  max-width: 200px;
}

.find-replace-section button {
  flex-shrink: 0;
}
input::placeholder {
  color: #666666;
  /* Default color for light mode */
}


/* Placeholder styles for light mode */
.light-placeholder::placeholder {
  color: #666666;
  /* Adjust this color as needed for visibility */
  opacity: 1;
  /* Ensures the placeholder is fully visible */
}

/* Placeholder styles for dark mode */
.dark-placeholder::placeholder {
  color: #aaaaaa;
  /* Adjust this color as needed for dark mode visibility */
  opacity: 1;
}


.find-replace-section {
  display: flex;
  align-items: center;
  /* Align inputs and button vertically */
  gap: 10px;
  /* Add spacing between items */
  flex-wrap: wrap;
  /* Ensure proper wrapping on smaller screens */
}

.find-replace-section input {
  flex: 1;
  /* Inputs take equal space */
  max-width: 200px;
  /* Ensure consistent sizing for inputs */
  padding: 8px;
  /* Match button's padding */
  box-sizing: border-box;
  /* Prevent overflow due to padding */
}

.find-replace-section button {
  padding: 8px 16px;
  /* Match the padding of the inputs */
  height: 38px;
  /* Ensure consistent height with inputs */
  line-height: normal;
  /* Avoid oversized text in the button */
  white-space: nowrap;
  /* Prevent text wrapping in the button */
}